import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import { ScrollToPlugin } from "gsap/ScrollToPlugin";

gsap.registerPlugin(ScrollTrigger, ScrollToPlugin);

document.addEventListener('DOMContentLoaded', function(){

    // INTRO

    if(document.querySelector('.section-intro')){

        const introItems = document.querySelectorAll('.section-intro__item');
        const sectionIntro = document.querySelector('.section-intro');
        const tl = gsap.timeline();
    
        for (let index = 0; index < introItems.length; index++) {
            const item = introItems[index];
    
            tl.fromTo(item,
                {
                    opacity: 0,
                },
                {
                    opacity: 1,
                    delay:  (index * 0.25),
                    duration: 0.5,
                }
            )
        }
    
        tl.play().delay(0.5)
    }


    // PROJECTS

    if(document.querySelector('.section-projects')){
        const projects = document.querySelectorAll('.card-project');

        for (let index = 0; index < projects.length; index++) {
            const item = projects[index];
            // let img = item.querySelector('.card-project__img');
            // let logo = item.querySelector('.card-project__logo');
            let title = item.querySelector('.card-project__title');
            let text = item.querySelector('.card-project__text');
            const tl = gsap.timeline();
            
            tl.to(
                item,
                {
                    scrollTrigger: {
                        trigger: item, 
                        start: 'top 95%',
                        toggleClass: 'is-visible', 
                    },
                },
            )        

            // tl.fromTo(
            //     img,
            //     {
            //         webkitClipPath: 'inset(0% 100% 100% 0%)',
            //         clipPath: 'inset(0% 100% 100% 0%)',
            //     },
            //     {
            //         webkitClipPath: 'inset(0% 0% 0% 0%)',
            //         clipPath: 'inset(0% 0% 0% 0%)',
            //         scrollTrigger: {
            //             trigger: item, 
            //             // scrub: 1,
            //             start: 'top 95%',
            //             // end: 'top 65%',
            //             toggleClass: 'is-visible', 
            //         },
            //     },
            // )
            
            // tl.fromTo(
            //     logo,
            //     {
            //         opacity: 0,
            //     },
            //     {
            //         opacity: 1,
            //         scrollTrigger: {
            //             trigger: item, 
            //             scrub: 1,
            //             start: 'top 85%',
            //             end: 'top 65%',
            //         },
            //     },
            // )
    
            tl.fromTo(
                title,
                {
                    y:-15, 
                    opacity: 0
                },
                {
                    y:0, 
                    opacity: 1,
                    scrollTrigger: {
                        trigger: title, 
                        scrub: 1.25,
                        start: 'top 100%',
                        end: 'bottom 80%',
                    },
                },
            )
    
            tl.fromTo(
                text,
                {
                    y:-35, 
                    opacity: 0
                },
                {
                    y:0, 
                    opacity: 1,
                    scrollTrigger: {
                        trigger: text, 
                        scrub: 1.5,
                        start: 'top 100%',
                        end: 'bottom 80%',
                    },
                },
            )
        }
    }


    // SERVICES

    if(document.querySelector('.section-services')){
        const services = document.querySelectorAll('.card-service');

        for (let index = 0; index < services.length; index++) {
            const item = services[index];
            const tl = gsap.timeline();
    
            tl.fromTo(
                item,
                {
                    y: (index * 50),
                    opacity: 0,
                    duration: 1,
                },
                {
                    y: 0,
                    opacity: 1,
                    scrollTrigger: {
                        trigger: item, 
                        scrub: 1,
                        start: 'top 90%',
                        end: 'top 70%',
                    },
                },
            )
        }
    }


    // REASONS

    if(document.querySelector('.section-reasons')){
        const tl = gsap.timeline();
        const reasons = document.querySelectorAll('.section-reasons__item');
        const jsReasonsTitle = document.querySelector('.jsReasonsTitle');
        const titleIcon = jsReasonsTitle.querySelector('.icon');
        const sectionReasons = document.querySelector('.section-reasons');
        const reasonsList = document.querySelector('.section-reasons__list');
        
        tl.fromTo(titleIcon,
            {
                x: 100,
            },
            {
                delay: 0.5,
                x: 0,
                scrollTrigger: {
                    trigger: sectionReasons,
                    start: 'top 75%',
                },
            }
        )
        
        tl.fromTo(titleIcon,
            {
                fill: '#262626',
                duration: 0.5,
            },
            {
                fill: '#C34F44',
                delay: 0.5,
                scrollTrigger: {
                    trigger: sectionReasons,
                    start: 'top 75%',
                },
            }
        )
           
        tl.fromTo(sectionReasons,
            {
                color: '#262626',
                backgroundColor: '#F6F6F6',
                duration: 0.5,
            },
            {
                delay: 1.25,
                color: '#fff',
                backgroundColor: '#151515',
                scrollTrigger: {
                    trigger: sectionReasons,
                    start: 'top 75%',
                },
            }
        )
    
        // tl.fromTo(
        //     reasonsList,
        //     {
        //         marginTop: 500,
        //         // y:0,
        //     },
        //     {
        //         marginTop:0,
        //         // y: -50,
        //         scrollTrigger: {
        //             trigger: reasonsList, 
        //             start: 'top 100%',
        //             end: 'top 65%',
        //             scrub: 1,
        //         },
        //     },
        // )
    
    
        for (let index = 0; index < reasons.length; index++) {
            const item = reasons[index];
    
            tl.fromTo(
                item,
                {
                    // top: (index == 0 ? (index * 100 + 200) : (index * 492))
                    marginTop: 492,
                },
                {
                    // top: (index * 100),
                    marginTop: 0,
                    delay: 2,
                    scrollTrigger: {
                        trigger: item, 
                        // start: 'top ' + (95 - index * 7.5) +'%',
                        // end: 'top ' + (60 - index * 7.5) +'%',
                        start: 'top 100%',
                        end: 'top 50%',
                        scrub: 1,
                    },
                },
            )
        }
    }


    // BITRIX
    
    if(document.querySelector('.section-bitrix')){
        const tl = gsap.timeline();
        const bitrixItems = document.querySelectorAll('.section-bitrix__item');
        const jsBitrixTitle = document.querySelector('.jsBitrixTitle');
        const sectionBitrix = document.querySelector('.section-bitrix');
    
        tl.to(jsBitrixTitle,
            {
                className: 'is-swapped',
                delay: 1,
                scrollTrigger: {
                    trigger: sectionBitrix,
                    start: 'top 75%',
                },
            }
        )
    
        for (let index = 0; index < bitrixItems.length; index++) {
            const item = bitrixItems[index];
            let content = item.querySelector('.section-bitrix__item-content');
            let xStart = '100%';
    
            if(index == 0){
                xStart = '-100%'
            } else if (index % 2 == 0) {
                xStart = '-100%'
            }
    
            tl.fromTo(item,
                {
                    x: xStart,
                    opacity: 0,
                },
                {
                    x: 0,
                    opacity: 1,
                    scrollTrigger: {
                        trigger: item,
                        start: 'top bottom',
                        end: 'top 75%',
                        scrub: true,
                    },
                }
            )
    
            tl.fromTo(content,
                {
                    opacity: 0,
                },
                {
                    opacity: 1,
                    scrollTrigger: {
                        trigger: item,
                        start: 'top 90%',
                        end: 'top 50%',
                        scrub: true,
                    },
                }
            )
        }
    }
    

    // PRODUCTS
        
    if(document.querySelector('.section-products')){
        const products = document.querySelectorAll('.card-product');
   
        for (let index = 0; index < products.length; index++) {
            const item = products[index];
            const tl = gsap.timeline();
    
            tl.to(
                item,
                {
                    scrollTrigger: {
                        trigger: item,
                        start: 'top 85%',
                        scrub: true,
                        toggleClass: 'is-visible', 
                    },
                }
            )
        }

    }    

    // if(document.querySelector('.section-services')){
    //     const tl = gsap.timeline();

    // }   

    // if(document.querySelector('.section-services')){
    //     const tl = gsap.timeline();

    // }
    

    if(document.querySelector('.jsProject')){

        let projects = document.querySelectorAll('.jsProject');

        for (let index = 0; index < projects.length; index++) {
            const project = projects[index];

            window.onload = function(){
                gsap.to(window, {duration: 0.1, scrollTo: 0});
                preloaderReset()
            }
            
            let introButton = project.querySelector('.jsProjectIntroButton');
            let preloader = project.querySelector('.jsProjectPreloader');
            let preloaderLogo = project.querySelector('.jsProjectPreloaderLogo');
            const body = document.querySelector('body');


            introButton.addEventListener('click', (e) =>{
                preloaderReset()
                gsap.to(window, {duration: 2,ease: "expo.inOut", scrollTo: preloader});
                gsap.fromTo(preloaderLogo, {opacity: 0}, {opacity: 1, delay: 1.75, duration: 1.5, ease: "expo.inOut"});

                setTimeout(() => {
                    gsap.to(preloaderLogo, {opacity: 0, duration: 1.5, ease: "expo.inOut"});
                }, 3500);

                setTimeout(() => {
                    gsap.to(preloader, {opacity: 0, duration: 1.5, ease: "expo.inOut"});
                }, 3750);

                setTimeout(() => {
                    body.style= '';
                }, 4750);
            })


            function preloaderReset(){
                body.style.overflow= 'hidden';
                gsap.to(preloader, { opacity: 1, duration: 0});
                gsap.to(preloaderLogo, { opacity: 1, duration: 0});
            }
        }

    }

    if(document.querySelector('.richness')){
        const tl = gsap.timeline();
        let typograpyImg = document.querySelector('.richness__typography-img');
        let screen1img = document.querySelector('.richness__screen--1 .richness__screen-img');
        let screen2img = document.querySelector('.richness__screen--2 .richness__screen-img');
        let screen3img = document.querySelector('.richness__screen--3 .richness__screen-img');
        let screen4img = document.querySelector('.richness__screen--4 .richness__screen-img--2');
        let screen5img = document.querySelector('.richness__screen--5 .richness__screen-img');
        let screen6img = document.querySelector('.richness__screen--6 .richness__screen-img');
                
        tl.fromTo(
            typograpyImg,

            {
                y: '100px',
                opacity: 0,
            },

            {
                y: '0%',
                opacity: 1,
                scrollTrigger: {
                    trigger: typograpyImg,
                    start: 'top 100%',
                    end: 'center 50%',
                    scrub: 1,
                },
            }
        )          


        tl.fromTo(
            screen1img,

            {
                x: '-10%',
            },
            
            {
                x: '0%',
                scrollTrigger: {
                    trigger: screen1img,
                    start: 'top 100%',
                    end: 'bottom 0%',
                    scrub: 1,
                },
            }
        )      

        tl.fromTo(
            screen2img,

            {
                y: '100px',
            },

            {
                y: '0%',
                scrollTrigger: {
                    trigger: screen2img,
                    start: 'top 100%',
                    end: 'center 50%',
                    scrub: 1,
                },
            }
        )          

        tl.fromTo(
            screen3img,

            {
                x: '0%',
            },

            {
                x: '-15%',
                scrollTrigger: {
                    trigger: screen3img,
                    start: 'top 100%',
                    end: 'bottom 0%',
                    scrub: 1,
                },
            }
        )     
              
        tl.fromTo(
            screen4img,

            {
                y: '100px',
            },

            {
                y: '0px',
                scrollTrigger: {
                    trigger: screen4img,
                    start: 'top 100%',
                    end: 'center 50%',
                    scrub: 1,
                },
            }
        )          

        tl.fromTo(
            screen5img,

            {
                x: '80%',
            },

            {
                x: '52%',
                scrollTrigger: {
                    trigger: screen5img,
                    start: 'top 100%',
                    end: 'center 50%',
                    scrub: 1,
                },
            }
        )     

        tl.fromTo(
            screen6img,

            {
                x: '-48%',
            },

            {
                x: '-24%',
                scrollTrigger: {
                    trigger: screen6img,
                    start: 'top 100%',
                    end: 'center 50%',
                    scrub: 1,
                },
            }
        )
    }

    
    if(document.querySelector('.olympia')){
        const tl = gsap.timeline();
        let screen1img1 = document.querySelector('.olympia__screen--1 .olympia__screen-img--1');
        let screen1img2 = document.querySelector('.olympia__screen--1 .olympia__screen-img--2');

        let typographyImg = document.querySelector('.olympia__screen--typography .olympia__typography-img');
        let typographyColorsWrapper = document.querySelector('.olympia__typography-colors');
        let typographyColors = document.querySelectorAll('.olympia__typography-color');

        let screen2img = document.querySelector('.olympia__screen--2 .olympia__screen-img');
        let screen3img = document.querySelectorAll('.olympia__screen--3 .olympia__screen-img');
        let screen4img = document.querySelector('.olympia__screen--4 .olympia__screen-img');
                
        tl.fromTo(
            screen1img1,

            {
                x: '-20%',
            },
            
            {
                x: '0%',
                scrollTrigger: {
                    trigger: screen1img1,
                    start: 'top 100%',
                    end: '25% 50%',
                    scrub: 1,
                },
            }
        )            

        tl.fromTo(
            screen1img2,

            {
                x: '35%',
            },
            
            {
                x: '0%',
                scrollTrigger: {
                    trigger: screen1img2,
                    start: 'top 100%',
                    end: 'center 50%',
                    scrub: 1,
                },
            }
        )      

        tl.fromTo(
            typographyImg,

            {
                y: 50,
                opacity: 0,
            },

            {
                y: 0,
                opacity: 1,
                scrollTrigger: {
                    trigger: typographyImg,
                    start: 'top 100%',
                    end: 'top 50%',
                    scrub: 1,
                },
            }
        )  

        for (let index = 0; index < typographyColors.length; index++) {
            const item = typographyColors[index];
    
            tl.fromTo(item,
                {
                    x: (index * 50),
                    opacity: 0,
                },
                {
                    x: 0,
                    opacity: 1,
                    scrollTrigger: {
                        trigger: typographyColorsWrapper,
                        start: 'top 95%',
                        end: 'top 50%',
                        scrub: true,
                    },
                }
            )
        }

        tl.fromTo(
            screen2img,

            {
                y: '100px',
            },

            {
                y: '0%',
                scrollTrigger: {
                    trigger: screen2img,
                    start: 'top 100%',
                    end: 'center 50%',
                    scrub: 1,
                },
            }
        )          

        for (let index = 0; index < screen3img.length; index++) {
            const item = screen3img[index];
    
            tl.fromTo(item,
                {
                    marginTop: (50 + (index * 50)),
                    opacity: 0,
                },
                {
                    marginTop: 0,
                    opacity: 1,
                    scrollTrigger: {
                        trigger: item,
                        start: 'top 100%',
                        end: 'top 60%',
                        scrub: true,
                    },
                }
            )
        }

              
        tl.fromTo(
            screen4img,

            {
                y: 30,
            },

            {
                y: 0,
                scrollTrigger: {
                    trigger: screen4img,
                    start: 'top 100%',
                    end: 'bottom 100%',
                    scrub: 1,
                },
            }
        )          
    }
})